#game-container {
  text-align: center;
  margin: 2em auto;
}

#game-container p {
  display: inline-block;
  margin-right: 1em;
  margin-bottom: 3em;
  font-weight: 500;
  font-size: 1.5em;
}

#game-container p span {
  font-weight: 300;
}

#game-container p#info {
  display: block;
  text-align: center;
  font-size: 2em !important;
  height: 2em;
  margin-bottom: 1em;
}

#scrambled-word {
  font-size: 3em !important;
  font-family: "Anton", sans-serif;
  text-transform: uppercase;
  display: block;
  margin-left: 48px;
  margin-bottom: 1em !important;
}

#scrambled-word p {
  display: block;
  width: 100%;
}

#user-guess {
  display: block;
  width: 20em;
  height: 3em;
  padding-left: 1em;
  margin: 0 auto 1.25em;
  border: 1px solid #000;
  outline-color: #e18a07;
  font-size: 1.1em;
}

.correct,
.incorrect {
  font-size: 1.5em;
  font-weight: 700;
}

.correct {
  color: green;
  display: block;
}

.incorrect {
  color: red;
  display: block;
}

/* .hidden {
  visibility: hidden;
} */

#guess-container button {
  cursor: pointer;
  width: 12em;
  height: 3em;
  outline: none;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 1.1em;
}

#submit {
  background: #fff;
  border: 2px solid #e18a07;
}

#submit:hover {
  background: #e18a07;
  color: #fff;
  transition: 0.2s background;
}

#next-btn {
  background: #fff;
  border: none;
  box-shadow: 1px 1px 1px #999;
}

#next-btn:hover {
  background: #07c7e1;
  color: #fff;
  transition: 0.2s box-shadow;
}

button:active {
  position: relative;
  top: 1px;
}

#copyright {
  text-align: center;
}
